:root {
    font-size: 62.5%;
    --web-color: #099d8d;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;

}

/* body {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
} */
.header-title {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
    text-align: center;
    /*si le ponemos classe al body, eliminar este text-align*/
    color: #ffffff;
    background-color: #099d8d;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    width: 100%;
    height: 6rem;
    opacity: 0.95;
    font-family: 'Rubik', sans-serif;
}


.container-link {
    background-color: #14d9c4;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style-type: none;
    width: 100%;
    height: 4.5rem;
    padding: 0.8rem 1.7rem 0.7rem 1.6rem;
    opacity: 0.9;
    font-size: 1.5rem;
    font-family: 'Rubik', sans-serif;

}

.link {
    color: #ffffff;
    text-decoration: none;
}

.container-hero {
    position: relative;
}

.imagen-hero {
    width: 100%;
    height: 100%;
}

.texto-hero {
    background-color: white;
    font-size: 18px;
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.39;
    letter-spacing: normal;
    color: black;
    position: absolute;
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);
    width: 290px;
    height: 80px;
    padding: 10px;
    text-align: center;
}


.box-equipo {
    padding: 30px 15px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}


.box-equipo-titulo {
    color: #14d9c4;
    display: flex;
    width: 89px;
    height: 45px;
    margin: 0 99.5px 15px 101.5px;
    font-family: 'Rubik';
    font-size: 24px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.88;
    letter-spacing: normal;
    text-align: center;
}

.box-equipo-texto {
    width: 290px;
    height: 384px;
    margin: 15px 0 0;
    font-family: "Open Sans", sans-serif;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: center;
    color: #54585a;
}

 .container-intro {
     width: 600px;
     height: 2520px;
     padding: 20px 15px;
     display: flex;
     flex-direction: column;
     justify-content: center;

  
 }

 .presentation {
     width: 100%;
     height: 37px;
     margin: 10px 15px;
     font-family: Rubik;
     display: flex;
     flex-direction: row;
     justify-content: center;
     font-size: 15px;
     font-weight: 600;
     font-style: normal;
     color: var(--web-color);
     
 }

 .box-self {
     width: 600;
     height: 600px;
     margin: 10px;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     text-align: center;
     
 }

.pic {
    width: 50px;
    height: 80px;
    margin: 3px 3px;
    border: solid 5px color#54585a;
    border-radius: 4px;
}

.name {
    padding: 10px 10px;
    text-align: center;
    font-size: 15px;
}

 .text-self {
     width: 290px;
     margin: 15px 0 0;
     padding: 10px 10px;
     font-family: "Open Sans", sans-serif;
     font-size: 16px;
     font-weight: normal;
     font-stretch: normal;
     line-height: 1.5;
     letter-spacing: normal;
    
 }

.listed-icons {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.fa {
    color: #099d8d;
    padding: 0 1px;
}

footer {
    width: 100%;
    border-top: 1px solid #b8b8b9;
    /*PONER MEDIAQUERY PARA EL GRIS CLARO*/
    padding-top: 10px;
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

small {
    color: #b8b8b9;
    text-align: center;
}

.footer-bar {
    color: #54585a;
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.footer-list {
    list-style: none;
}

.footer-logo {
    width: 115px;
    height: 45px;
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.box-fort-debil {
    background-color: #b8b8b9;
}

.box2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: 250px;
    line-height: 1.5;
    background-color: #b8b8b9;

}

.titulo-1 {
    font-family: "Rubik";
    color: #099d8d;
    width: 290px;
    height: 45px;
    font-size: 22px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.05;
    letter-spacing: normal;
    text-align: center;
    padding-bottom: 50px;
    padding-top: 20px;
}

.subtitulo1 {
    color: #54585a;
    font-family: "Open Sans", sans-serif;
    font-size: 16px;
    width: 290px;
    line-height: 1.5;

}